    left: 300px;
    top: -47px;
  }
}
/* Definición de variables para centralizar la paleta de colores */
:root {
  --verde-oscuro: #2e5d47;
  --verde-claro: #a0d6b4;
  --blanco: #eae8e8;
  --azul-acento: #00bcd4;
  --azul-acento-hover: #26c6da;
  font-family: "Helvetica Neue", sans-serif;
}

/* Contenedor principal de la sección */
.contenedor-seccion {
  max-width: 1600px;
  margin: 0 auto;
  padding: 2rem;
  margin-top: -6vh;
  margin-bottom: 2rem;
}

/* Sección de servicios */
.servicios-iguazu {
  background-color: #0A354C;
  padding: 6rem 2rem;
  color: var(--blanco);
  text-align: center;
  position: relative;
  overflow: hidden;
}

/* Encabezado de la sección */
.titulo-seccion {
  padding: 2rem;
}

.titulo-seccion h2 {
  font-size: 3.5rem;
  margin-bottom: 0.5rem;
}

.titulo-seccion .subtitulo {
  font-size: 1.8rem;
  color: #fff;
  font-weight: 32;
}

/* Tarjetas de servicio minimalistas */
.cards-servicios {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}

.card-servicio {
  border-radius: 10px;
  transition: transform 0.3s ease;
  text-align: center;
}

.card-servicio:hover {
  transform: translateY(-5px);
}

/* Estilos de texto y botones en las tarjetas */
.card-servicio h3 {
  font-size: 2.5rem;
  line-height: 3rem;
  margin: 1rem 0;
}

.card-servicio p {
  font-size: 1.8rem;
  line-height: 1.4;
  margin: 1rem 0;
}

.boton-info,
.boton-principal {
  display: inline-block;
  padding: 0.6rem 1.2rem;
  border-radius: 30px;
  text-decoration: none;
  background: var(--azul-acento);
  color: var(--blanco);
  transition: background 0.3s ease;
}

.boton-info:hover,
.boton-principal:hover {
  background: var(--azul-acento-hover);
}

/* Bloque de descripción principal */
.descripcion-principal {
  border-radius: 10px;
  padding: 2rem;
  max-width: 1000px;
  margin: 4rem auto 10rem;
  text-align: left;
}

.descripcion-principal h2 {
  font-size: 3rem;
  text-transform: uppercase;
  margin-bottom: 1rem;
}

.descripcion-principal p {
  font-size: 1.8rem;
  line-height: 1.4;
  margin-bottom: 1.5rem;
}

