/* Diseño base de Uiverse.io adaptado para cards más grandes y naturales */
.card {
  position: relative;
  width: 100%; /* Ajustado a 100% para ser más flexible */
  max-width: 450px; /* Se define un máximo para la tarjeta */
  height: auto; /* Ajusta la altura de acuerdo con el contenido */
  border-radius: 10px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.15);
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  transition: all 0.6s cubic-bezier(0.23, 1, 0.32, 1);
  cursor: pointer;
  margin: 0 auto;
}

.card:hover {
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.25);
}

.card__content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  padding: 20px;
  box-sizing: border-box;
  background-color: var(--blanco);
  opacity: 0;
  transition: all 0.6s cubic-bezier(0.23, 1, 0.32, 1);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  text-align: left;
}

.card:hover .card__content {
  opacity: 1;
}

.card__title {
  margin: 0;
  font-size: 28px;
  color: var(--verde-oscuro);
  font-weight: 700;
}

.card__description {
  margin-top: 10px;
  font-size: 16px;
  color: #555;
  line-height: 1.4;
}

.card:hover svg {
  transform: scale(0);
  transition: transform 0.3s ease;
}

/* Estilos para el contenedor de la imagen */
.imagen-contenedor {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.imagen-contenedor img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  transition: opacity 0.6s ease;
}

.card:hover .imagen-contenedor img {
  opacity: 0.5;
}

/* Media Queries para tarjetas */
@media (max-width: 768px) {
  .card {
    max-width: 100%; /* Asegura que las tarjetas no excedan el contenedor */
    height: 250px;
  }
  .card__title {
    font-size: 22px;
  }
  .card__description {
    font-size: 14px;
  }
}
@media (max-width: 576px) {
